import { createRouter,createWebHistory } from "vue-router"
import TheHome from "../components/TheHome.vue"
import TheLogin from "../components/TheLogin.vue"
import TheRegister from "@/components/TheRegister.vue"
import PageMember from "../components/PageMember.vue"
import ProductShow from "@/components/ProductShow.vue"
import CartShow from "@/components/CartShow.vue"
import CartList from "@/components/CartList.vue"
import LaptopCategory from "@/category/LaptopCategory.vue"
import TheOrder from "@/components/TheOrder.vue"

const routes = [
    {
        path:'/',
        name:'Home',
        component:TheHome
    },
    {
        path:'/login',
        name:'Login',
        component:TheLogin
    },
    {
        path:'/register',
        name:'Register',
        component:TheRegister
    },
    {
        path: '/pagemember',
        name: 'PageMember',
        component:PageMember
    },
    {
        path: '/pageshow/:pdId',
        name: 'ProductShow',
        component:ProductShow
    },
    {
        path:'/CartShow/:cartId',
        name:'CartShow',
        component:CartShow
    },
    {
        path:'/CartList',
        name:'CartList',
        component:CartList
    },
    {
        path:'/category/laptop',
        name:'LaptopCategory',
        component:LaptopCategory
    },
    {
        path:'/order/:cartId',
        name:'theOrder',
        component:TheOrder
    }
]
const router = createRouter({
    history:createWebHistory(process.env.BASE_URL),routes
})
export default router
