<template>
  <nav
    class="navbar navbar-expand-lg sticky-top"
    id="nav"
    style="background-color: #ffffff" 
  >
    <div class="container">
      <a class="navbar-brand" href="#">
        <img
          src="../assets/kameraider.jpeg"
          alt=""
          width="50px"
          class="rounded"
        />
      </a>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNav"
        aria-controls="navbarNav"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNav">
        <ul class="navbar-nav me-auto">
          <router-link to="/" class="nav-item" style="text-decoration: none">
            <a
              class="nav-link active h3 text-dark fw-bold"
              aria-current="page"
              href="#"
              style="color: aliceblue"
              >Minions
            </a>
          </router-link>
          <li class="nav-item">
            <router-link to="/pagemember" style="text-decoration: none">
              <div class="nav-link fw-bold">{{ memName }}</div>
            </router-link>
          </li>
        </ul>
        <div class="d-flex mt-3">
          <p  v-if="memName === null">
            <router-link to="/Login" style="text-decoration: none">
              <i class="bi bi-person-fill text-dark h3 fw-bold"></i>
            </router-link>
          </p>
          <p  v-else>
            <a href="#" @click="memlogout" style="text-decoration: none">
              <div class="nav-link">Logout</div>
            </a>
          </p>
          <CardInfo />
        </div>
      </div>
    </div>
  </nav>
</template>


<script>
import { EventBus } from "@/even-bus";
import axios from "axios";
import CardInfo from "./CardInfo.vue";
axios.defaults.withCredentials = true;

export default {
  name: "MainMenu",
  components: {
    CardInfo,
  },
  data() {
    return {
      memName: null,
      backendMessage: null,
    };
  },
  mounted() {
    // เมื่อเริ่ม mount ให้อ่านค่าจาก sessionStorage
    this.memName = sessionStorage.getItem("memName");
    // เมื่อ EventBus เกิดสัญญาณ loginok ให้อ่านข้อมูลจาก sessionStorage ใหม่
    EventBus.on("loginok", () => {
      this.memName = sessionStorage.getItem("memName");
    });
  },
  // เมื่อ Unmount ให้ปิดการเชื่อมต่อกับEventBus
  beforeUnmount() {
    EventBus.off("loginok");
  },
  methods: {
    async memlogout() {
      const cf = window.confirm("ต้องการออกจากระบบ?");
      EventBus.emit("memlogout");
      if (cf) {
        try {
          const response = await axios.get(
            `http://localhost:3000/members/logout`
          );
          this.backendMessage = response.data.messagelogout;
          if (this.backendMessage == "success") {
            // ลบเฉพาะ sessionStorage ที่ระบุ
            // sessionStorage.removeItem('memName');
            // ลบ sessionStorage ทั้งหมด
            sessionStorage.clear();
            this.memName = null;
            this.$router.push("/");
          }
        } catch (err) {
          console.log(err);
        }
      }
    },
  },
};
</script>

<style>
#nav {
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}
</style>
