<script>
import axios from "axios";
import { EventBus } from "../even-bus";
axios.defaults.withCredentials = true;

export default {
  name: "ProductShow",
  data() {
    return {
      products: [],
      id: null,
      memEmail: null,
      cartId: null,
      details: [],
    };
  },
  mounted() {
    this.id = this.$route.params.pdId;
    console.log(this.id);
    axios
      .get(`http://localhost:3000/products/${this.id}`)
      .then((res) => {
        this.products = res.data;
        console.log(this.products);
      })
      .catch((err) => {
        console.error(err);
      });
    axios
      .get(`http://localhost:3000/products/detail/${this.id}`)
      .then((res) => {
        this.details = res.data;
        console.log(this.details);
      })
      .catch((err) => {
        console.error(err);
      });
  },
  methods: {
    // ฟังก์ชันจัดรูปแบบราคา
    formattedPrice(price) {
      return new Intl.NumberFormat('th-TH').format(price);
    },
    async chkLogin() {
      console.log("chkLogin");
      await this.chkSession();
      if (this.memEmail == null) {
        alert("ต้อง Login เข้าระบบก่อน");
        return false;
      }
      await this.chkCart();
      if (this.cartId == null) {
        await this.addCart();
      }
      this.addCartDtl();
    },
    async chkSession() {
      await axios
        .get(`http://localhost:3000/members/getss`)
        .then((res) => {
          this.memEmail = res.data.email;
          console.log(this.memEmail);
        })
        .catch((err) => {
          console.error(err);
        });
    },
    async chkCart() {
      console.log("chkCart");
      await axios
        .get(`http://localhost:3000/carts/chkcart`)
        .then((res) => {
          console.log("Cart" + res.data);
          this.cartId = res.data.id;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    async addCart() {
      console.log("addCard");
      let customer = {
        cusId: this.memEmail,
      };
      try {
        const response = await axios.post(
          `http://localhost:3000/carts/addcart`,
          customer
        );
        this.backendMessage = response.data.messageAddCart;
        this.cartId = response.data.messageAddCart;
        console.log(response.data);
      } catch (err) {
        console.log(err);
      }
    },
    async addCartDtl() {
      console.log("addCartDtl");
      let cartdtl = {
        cartId: this.cartId,
        pdId: this.id,
        pdPrice: this.products[0].pdPrice,
      };
      try {
        const response = await axios.post(
          `http://localhost:3000/carts/addcartdtl`,
          cartdtl
        );
        EventBus.emit("cartdtlOK");
        this.backendMessage = response.data.messageAddCartDtl;
        console.log(response.data);
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

