<template>
    <div v-if="cartId != null" >
        <!-- <button  class="btn " @click="$router.push(`/cartshow/${cartId}`)">
            <i class="bi bi-bag-fill"></i>
            {{cartId}} [{{ qty }}] - {{ money }}฿
        </button> -->
        <button type="button" class="btn position-relative" @click="$router.push(`/cartshow/${cartId}`)">
            <i class="bi bi-bag-fill"></i>
            <span class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                {{ qty }}
            </span>
        </button>

    </div>
</template>

<script>
import axios from 'axios';
axios.defaults.withCredentials = true
import { EventBus } from "../even-bus"
export default {
    name: "CartInfo",
    data() {
        return {
            cartId: null,
            qty: 0,
            money: 0
        }
    },
    async mounted() {
    await this.sumCart();
    EventBus.on('cartdtlOK', () => {
        this.sumCart();
    });
    EventBus.on('memlogout', () => {
        this.clearCart();
    });

    // Polling เพื่ออัพเดตจำนวนสินค้า
    setInterval(() => {
        this.sumCart();
    }, 2000); // อัพเดตทุก ๆ 2 วินาที
},
    methods: {
        async sumCart() {
            console.log('sumCart')
            await axios.get(`http://localhost:3000/carts/sumcart`)
                .then(res => {
                    console.log("SumCart" + res.data)
                    this.cartId = res.data.id
                    this.qty = res.data.qty
                    this.money = res.data.money
                })
                .catch(err => {
                    console.error(err);
                });
        },
        clearCart()
        {
            console.log('Clear Cart')
            this.cartId = null
            this.qty = 0
            this.money = 0
        }
    }
}
</script>


