<template>
    <table class="table align-middle mt-3">
      <thead class="">
        <tr class="bg-danger text-light" style="border-radius: 100px">
          <td></td>
          <td>สินค้า</td>
          <td>รายการสินค้า</td>
          <td class="text-end">ราคาต่อหน่วย</td>
          <td class="text-center">จำนวน</td>
          <td class="text-end">เป็นเงิน</td>
          <td></td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(ctd, pdId) in cartDtl" :key="pdId">
          <td>{{ ctd.row_number }}</td>
          <td>
            <img
              :src="`http://localhost:3000/img_pd/${ctd.pdId}/${ctd.pdId}-1.jpg`"
              class="img-fluid rounded"
              alt=""
              width="100"
            />
          </td>
          <td>{{ ctd.pdName }}</td>
          <td class="text-end">{{ formattedPrice(ctd.price) }}</td>
          <td class="text-center">{{ ctd.qty }}</td>
          <td class="text-end">{{ formattedPrice(ctd.price * ctd.qty) }}</td>
        </tr>
      </tbody>
    </table>
    <h1>เพิ่มที่อยู่จัดส่ง</h1>
    <form class="bg-light container p-4 rounded" @submit.prevent="orderItems">
      <div class="mb-3">
        <label class="form-label">ชื่อ-นามสกุล</label>
        <input type="text" class="form-control" v-model="customerName" />
      </div>
      <div class="mb-3">
        <label class="form-label">เบอร์โทรศัพท์</label>
        <input type="tel" class="form-control" v-model="phone" />
      </div>
      <div class="mb-3">
        <label class="form-label">ที่อยู่</label>
        <input type="text" class="form-control" v-model="address" />
      </div>
      <div class="row justify-content-center">
        <div class="col mb-3">
          <label class="form-label">ตำบล/แขวง</label>
          <input type="text" class="form-control" v-model="subDistrict" />
        </div>
        <div class="col mb-3">
          <label class="form-label">จังหวัด</label>
          <input type="text" class="form-control" v-model="province" />
        </div>
      </div>
      <div class="row justify-content-center">
        <div class="col mb-3">
          <label class="form-label">อำเภอ / เขต</label>
          <input type="text" class="form-control" v-model="district" />
        </div>
        <div class="col mb-3">
          <label class="form-label">รหัสไปรษณีย์</label>
          <input type="text" class="form-control" v-model="postalCode" />
        </div>
      </div>
  
      <button type="submit" class="btn btn-primary">สั่งซื้อสินค้า</button>
    </form>
    <div v-if="backendMessage == 'success'">
        <div class="alert alert-secondary">
            สั่งซื้อสำเร็จ
        </div>
    </div>
    <div v-else-if="backendMessage == 'fail'">
        <div class="alert alert-danger">
            สั่งซื้อไม่สำเร็จ
        </div>
    </div>
    <!-- Detail -->
  </template>
  
  <script>
  import axios from "axios";
  import { v4 as uuidv4 } from 'uuid';

  export default {
    name: "theOrder",
    data() {
      return {
        cartDtl: [], // รับข้อมูล Detail
        cartId: null,
        memEmail: null,
        customerName: "",
        phone: "",
        address: "",
        subDistrict: "",
        district: "",
        province: "",
        postalCode: ""
      };
    },
    async mounted() {
      await this.chkSession();
      this.cartId = this.$route.params.cartId;
      await this.getCartDtl();
    },
    methods: {
      async getCartDtl() {
        console.log("Get CartCartDtl");
        await axios
          .get(`http://localhost:3000/carts/getcartdtl/${this.cartId}`)
          .then((res) => {
            console.log("CartDtl \n", res.data);
            this.cartDtl = res.data;
          })
          .catch((err) => {
            console.error(err);
          });
      },
      async chkSession() {
        await axios
          .get(`http://localhost:3000/members/getss`)
          .then((res) => {
            console.log("Session data:", res.data);
            this.memEmail = res.data.email;
            console.log("Updated memEmail:", this.memEmail);
          })
          .catch((err) => {
            console.error(err);
          });
      },
      formattedPrice(price) {
        return price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      },
      async updateCartCf(){
        console.log("updateCartCf")
        try {
            const response = await axios.post(`http://localhost:3000/orders/updateCart`, { id: this.cartId });
            console.log("Cart updated:", response.data);
        } catch(err){
            console.error(err);
        }
      },
      async orderItems() {
        if (this.cartDtl.length === 0) {
          alert("ไม่มีสินค้าในตะกร้า");
          return;
        }
        const orderId = uuidv4();
        const orderData = {
            orderId,
          memEmail: this.memEmail,
          customerName: this.customerName,
          phone: this.phone,
          address: this.address,
          subDistrict: this.subDistrict,
          district: this.district,
          province: this.province,
          postalCode: this.postalCode,
          cartId: this.cartId,
        };
  
        try {
          const response = await axios.post(
            "http://localhost:3000/orders", orderData
          );
          console.log("Order submitted:", response.data);
          this.backendMessage = response.data.messageOrder
          await this.updateCartCf();
          // สามารถทำการนำผู้ใช้ไปยังหน้าถัดไปหรือรีเซ็ตฟอร์มได้
        } catch (error) {
          console.error("Error submitting order:", error);
          alert("Failed to submit order.");
        }
        this.$router.push("/");
      },
    },
  };
  </script>
  
  <style></style>
  