<template>
   <div class="row g-0 p-0" id="card-sc">
      <div v-for="(pd, pdId) in products" :key="pdId" class="col-lg-3 g-3">
        <router-link
          :to="{ name: 'ProductShow', params: { pdId: pd.pdId } }"
          style="text-decoration: none; color: #000"
        >
          <div
            class="card mt-3"
            style="
              width: 100%;
              background-color: #ffff;
              border: none;
              box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
            "
          >
            <img
              :src="`http://localhost:3000/img_pd/${pd.pdId}/${pd.pdId}-1.jpg`"
              class="card-img-top img-fluid rounded"
              alt=""
            />
            <div class="card-body">
              <h5 class="card-title">{{ pd.pdName }} {{ pd.pdId }}</h5>
              <p class="card-text">
                {{ pd.thebrand ? pd.thebrand.brandName : "No Brand" }} - Price
                {{ pd.pdPrice }}
              </p>
            </div>
          </div>
        </router-link>
      </div>
    </div>
</template>

<script>


import axios from 'axios';
export default {
    name : "LaptopCategory",
    data() {
      return{
        products:[]
      }
    },
    mounted() {
    axios
      .get("http://localhost:3000/products/type/laptop")
      .then((res) => {
        this.products = res.data;
      })
      .catch((err) => {
        console.error(err);
      });
  },
}
</script>

