<script>
import { EventBus } from "@/even-bus"; // แก้ไขที่นี่ให้ตรงกับ path ของ EventBus
import axios from "axios";
// การทำงานกับ session หรือ authentication ต้องส่งข้อมูล cookies พร้อมกับ request
axios.defaults.withCredentials = true;

export default {
  name: "TheLogin",
  data() {
    return {
      loginname: null,
      password: null,
      backendMessage: null,
      memEmail: null,
      memName: null,
      dutyId: null,
    };
  },
  // เมื่อ component เริ่มทำงาน
  mounted() {
    // ถ้ามีข้อมูลใน sessionStorage ให้ redirect ไปที่หน้า PageMember
    if (sessionStorage.getItem("memEmail") != null) {
      this.$router.push("/pagemember");
    }
  },
  methods: {
    async handleSubmit() {
      let members = {
        loginname: this.loginname,
        password: this.password,
      };

      try {
        // ส่งข้อมูล loginname และ password ไปที่ backend
        const response = await axios.post(
          `http://localhost:3000/members/login`,
          members
        );
        this.backendMessage = response.data.messagelogin;
        if (this.backendMessage == "success") {
          await this.chkSession();
          EventBus.emit("loginok");
          this.$router.push("/pagemember");
        } else {
          console.log("Login failed");
        }
      } catch (err) {
        console.log(err);
      }
    },
    async chkSession() {
      try {
        await axios.get(`http://localhost:3000/members/getss`).then((res) => {
          this.memEmail = res.data.email;
          this.memName = res.data.name;
          this.dutyId = res.data.duty;
          sessionStorage.setItem("memName", res.data.name);
        });
      } catch (err) {
        console.log("Error fetching session data:", err);
      }
    },
  },
};
</script>

